<template>
  <transition name="detail-transition" appear>
    <div :class="containerClass">
      <img class="background-image" :src="getBackground()">
      <div class="top">
        <custom-button :active="activeSection !== 'series'" round icon="back" :dark="dark" />
        <div class="basic-info">
          <div class="title">
            {{ title }}
          </div>
          <div v-if="imdbRating" class="imdb-rating">
            <div class="logo"></div>
            <div class="text">
              {{ imdbRating }}
              <span class="text-smaller">/10</span>
            </div>
          </div>
        </div>
      </div>
      <div class="details">
        <div class="picture">
          <img :src="getPoster()">
        </div>
        <span class="episodes-number">{{ numberOfEpisodes }} Episodes</span>
        <span class="episodes-type">HBO OD</span>
      </div>
      <div class="description">
        <div class="main">
          <card-row
            :card-theme="'dark'"
            :card-type="'catchup'"
            :card-width="300"
            :height="{ closed: 328, open: 328 }"
            :display-count="false"
            :focused="episodesFocused"
            :items="episodes"
            :selected="activeCard"
            :expanded="true"
            :active-row="true"
          />
          </div>
        </div>
      </div>
      <div v-if="this.$slots.more" class="more-wrapper">
        <div class="more">
          <span>HBO OD</span>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapState } from 'vuex'
import find from 'lodash/fp/find'
import findIndex from 'lodash/fp/findIndex'
import pick from 'lodash/fp/pick'
import { getImage } from 'helpers/image'
import { emitKeyPress } from 'helpers/keyHold'

import CustomButton from 'components/Button'
import RegisterKeyHandler from 'mixins/RegisterKeyHandler'
import CardRow from 'containers/general/CardRow'
import vodPosterImage from 'assets/images/placeholders/vod_poster_placeholder_248x366.png'
import vodEventImage from 'assets/images/placeholders/vod_event_300x168.png'
import vodEventDetail from 'assets/images/placeholders/vod_event_detail_1920x1080.png'
import HistoryManager from 'mixins/HistoryManager'

const IMG_SMALL = 'STB_FHD'
const IMG_LARGE = 'STB_XL'
const TYPE_VOD_POSTER = 'VOD_POSTER_21_31'
const TYPE_EVENT = 'EVENT_16_9'

export default {
  name: 'SeasonDetail',
  mixins: [ RegisterKeyHandler, HistoryManager ],
  components: { CustomButton, CardRow },
  data: () => ({
    channelLogoImageUrl: '',
    imdbRating: 9,
    backgroundImageUrl: `/static/uc/images/fake-video.jpg`,
    activeSection: 'series',
    episodesFocused: true,
    activeCard: 0
  }),
  props: {
    backActive: Boolean,
    dark: {
      type: Boolean,
      default: true
    },
    endTime: Object,
    fullActive: Boolean,
    rating: String,
    startTime: Object,
    title: String
  },
  computed: {
    containerClass () {
      return [
        'container',
        { 'is-dark': this.dark }
      ]
    },
    numberOfEpisodes () { return this.season ? this.season.numberOfEpisodes : '' },
    season () {
      return find({id: this.$route.params.pageData.seasonId})(this.seasons)
    },
    episodes () {
      if (this.season && this.season !== -1) {
        return this.season.episodes.map((episode, index) => {
          return {
            logoUrl: getImage(episode.images, IMG_SMALL, TYPE_EVENT, vodEventImage),
            progress: episode.progress,
            // subtitle: `EPISODE ${episode.id}`,
            subtitle: `EPISODE ${this.season.episodes.length - index}`,
            expanded: true,
            ...pick(['id', 'title', 'year', 'inFavorites'], episode)
          }
        })
      } else {
        return [{ episodes: [] }]
      }
    },
    ...mapState({
      asset: state => state.vod.detail,
      seasons: state => state.vod.seasons
    })
  },
  methods: {
    getPoster () {
      return getImage(this.asset.images, IMG_SMALL, TYPE_VOD_POSTER, vodPosterImage)
    },
    getBackground () {
      return getImage(this.asset.images, IMG_LARGE, TYPE_EVENT, vodEventDetail)
    },
    handleKey (key) {
      switch (key) {
        case 'BACK':
          this.doHistoryBack()
          break
        case 'LEFT':
          if (this.activeCard > 0) {
            this.moveRow(key)
          }
          break
        case 'RIGHT':
          this.moveRow(key)
          break
        case 'LEFT_HOLD': // j for web
        case 'RIGHT_HOLD': // k for web
          if (emitKeyPress({ delay: 50 })) {
            this.moveRow(key === 'LEFT_HOLD' ? 'LEFT' : 'RIGHT')
          }
          break
        case 'UP':
          // this.backActive = true
          this.activeSection = 'back'
          this.episodesFocused = false
          break
        case 'DOWN':
          // this.backActive = false
          this.activeSection = 'series'
          this.episodesFocused = true
          break
        case 'HOLD_STOP': // l for web
          break
        case 'OK':
          this.selectCard()
          break
      }
    },
    selectCard () {
      if (this.activeSection === 'back') {
        this.$router.push({
          name: 'VodDetail',
          params: { asset: this.asset }
        })
      } else {
        // here we need to use series detais
        this.$router.push({
          name: 'EpisodeDetail',
          params: {
            seasonId: this.$route.params.pageData.seasonId,
            episodeId: this.episodes[this.activeCard].id
          }
        })
      }
    },
    moveRow (direction) {
      const offset = direction === 'LEFT' ? -1 : 1
      this.activeCard = Math.min(Math.max(this.activeCard + offset, 0), this.episodes.length - 1)
    }
  },
  mounted () {
    if (this.$route.params.pageData.episodeId) this.activeCard = findIndex({id: this.$route.params.pageData.episodeId})(this.episodes)
  }
}
</script>

<style lang="scss" scoped>
@import "variables";

$more-offset: 960rem;

.container {
  height: 100%;
  width: 1920rem;
  transition: transform $transition;

  &.is-dark {
    .details {
      background: rgba($grey-darker, .8);
    }

    .description, .more-wrapper {
      background: rgba($grey-darker, 1);
    }

    .small-title { color: $white; }
    .main-title { color: $white; }
    .item { color: rgba($white, .7); }
    .main-text { color: rgba($white, .7); }
  }
}

  .background-image {
    height: 1080rem;
    left: 0;
    position: absolute;
    top: 0;
    width: 1920rem;
    z-index: -1;
  }

  .top {
    display: flex;
    justify-content: space-between;
    height: 505rem; //  410rem
    padding: 62rem 186rem 0 66rem;
    background: rgba(46, 46, 46, 0.95);
  }
    .imdb-rating {
      display: flex;
      align-items: center;
      margin-top: 30rem;
      margin-left: auto;
    }
      .logo {
        background-image: url('data:image/png;base64,');
        background-size: 100%;
        height: 33rem;
        width: 68rem;
      }
      .text {
        color: $white;
        font-size: 28rem;
        line-height: 36rem;
        margin-left: 14rem;
        text-shadow: 2rem 2rem 0 $black;
        &-smaller {
          font-size: 20rem;
        }
      }

  .basic-info {
    display: flex;
    flex-direction: row;
    padding: 0 0 25rem 208rem;
    height: 95rem;
    margin-top: auto;
    width: 100%;
    color: white;
  }

  .title {
    font-size: 80rem;
    font-weight: 200;
  }

  .details {
    background: rgba($white-lighter, .72);
    border-top: 5rem solid $grey-lighter;
    height: 135rem;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 184rem 0 66rem;
    font-size: 28rem;
    font-weight: 200;
    color: white;
    .episodes-number {
      padding-left: 57rem;
    }
    .episodes-type { margin-left: auto;}
    .picture {
      width: 250rem;
      height: inherit;
      position: relative;
      img {
        position: absolute;
        width: 250rem;
        height: 360rem;
        bottom: -20rem;
        background-color: $blue-medium;
      }
    }
  }

  .description {
    background: rgba($white-lighter, .8);
    display: flex;
    height: 440rem;
    color: white;
  }

    .main {
      padding: 80rem 30rem 40rem 66rem;
      position: relative;
      width: 100%;
    }
.detail-transition {
  &-enter, &-leave-to { opacity: 0; }
  &-enter-to, &-leave { opacity: 1; }
  &-enter-active { transition: opacity .6s ease-in-out; }
  &-leave-active { transition: opacity .2s ease-in-out; }
}
</style>



